import React, { Component } from 'react';
import Footer from './Footer';
import img from '../Images/PP.jpg';

const creators = [
  {
    url: 'images/img.png',
    nom: 'PERATOU CATUSSIA',
    descri: 'Je suis la directrice de La Maison Des Artistes, je fixe les objectifs de lentreprise, jorganise et coordonne le travail. Jai de la rigueur, de lorganisation et du charisme.',
    poste: 'DIRECTRICE',
  }, {
    url: 'images/img.png',
    nom: 'FARGEOT MARIELLE',
    descri: 'Je suis la sous directrice de La Maison Des Artistes, jassiste la directrice dans ses fonctions dencadrement et gre les ateliers formation. Jai de lorganisation, je suis oprationnelle et loyale.',
    poste: 'SOUS DIRECTRICE',
  }, {
    url: 'images/img.png',
    nom: 'LADREZEAU YANN',
    descri: 'Je suis le responsable du ple communication internet et rseaux sociaux.',
    poste: 'RESPONSABLE COMMUNICATION',
  }, {
    url: 'images/img.png',
    nom: 'BRANDON DIMITRI',
    descri: 'Je suis lattach de presse de La Maison Des Artistes, je suis charg de la communication orale et crite de lentreprise avec les diffrentes presses. Je suis littraire, jai une aisance  la communication et je suis dynamique.',
    poste: 'ATTACHE DE PRESSE',
  }, {
    url: 'images/img.png',
    nom: 'LADREZEAU LAURY',
    descri: 'Je suis le responsable du ple achat vente de La Maison Des Artistes. Je gre les achats et les ventes, je tiens  jour les documents comptables, je travaille en troite collaboration avec les ples production et commercialisation. Je suis intgre, organis et jai un niveau en math assez correct.',
    poste: 'RESPONSABLE ACHAT-VENTE',
  }, {
    url: 'images/img.png',
    nom: 'THOMAS MEDHY',
    descri: 'Je suis le responsable du ple production 1 de La Maison Des Artistes. Je gre la production quantitativement et qualitativement, je fixe le prix des produits fabriqusJe suis cratif, minutieux et productif.',
    poste: 'RESPONSABLE PRODUCTION',
  }, {
    url: 'images/img.png',
    nom: 'SELBONNE MARVIN',
    descri: 'Je suis le second responsable du ple production de La Maison Des Artistes.  Je gre la production quantitativement et qualitativement, je fixe le prix des produits fabriqus.',
    poste: 'RESPONSABLE PRODUCTION',
  }, {
    url: 'images/img.png',
    nom: 'CHAVEZ DA SILVA KARIM',
    descri: 'Je suis le responsable du ple commerciale de La Maison Des Artistes. Jorganise la distribution, gre la commercialisation et le taux de satisfaction du client.Je suis vendeur, dynamique et passionn..',
    poste: 'RESPONSABLE COMMERCIALE',
  },];
class Createurs extends Component {
  state = {
    modalVisibility: false,
    position: 0,
    nbr: 0
  }

  showModal = (i) => {
    this.setState({
      modalVisibility: true,
      nbr: i,
    })

  }
  hideModal = () => {
    this.setState({
      modalVisibility: false
    })
  }

  render() {

    const { modalVisibility } = this.state;
    return (

      <div id="App">
        <p className="TextLoremH">"Jai t initi au dveloppement durable. Cela me plat. De
plus, jai dvelopp des talents artistiques et un esprit de crativit mconnu 
la base."
        </p>
        {
          modalVisibility ?

            <div id="modal-container" onClick={this.hideModal}>
              <div id="creatorprofile" onClick={e => {
                e.stopPropagation();
              }  // Do something
              }>
                <img className="creatorpp" src={creators[this.state.nbr].url} alt={creators[this.state.nbr].nom} />
                <div id="creatorinfo">
                  <h1>{creators[this.state.nbr].nom}</h1>
                  <h2>{creators[this.state.nbr].poste}</h2>
                  <p>"{creators[this.state.nbr].descri}"</p>
                </div>
              </div>
              <img alt="" id="croix" onClick={this.hideModal} src="https://smartbees.fr/assets/img/x-min.png" />
            </div>

            : null
        }
        <ul id="Cards-container">
          {creators.map((Card, i) => {
            return (
              <li className="Cards" onClick={() => this.showModal(i)} key={i}><img className="creatorspp" src={Card.url} alt={Card.nom} /><p>{Card.nom}</p></li>
            )
          })}
        </ul>
        <Footer/>
      </div>
    );
  }
}

export default Createurs;


